import React from "react";
import Header from "../commonComponent/header";
import SideBar from "../commonComponent/sideBar";
import { Link, useLocation, useParams } from "react-router-dom";
import { useQuery } from "@tanstack/react-query";
import {
  maintenanceDetails,
  statusUpdateMaintinanc,
} from "../../apiServices/home/homeHttpService";
import moment from "moment";
import { showAlert } from "../commonComponent/alertManager";
import { useAlert } from "react-alert";

function MaintenanceDetails() {
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  let { id } = useParams();
  const alert = useAlert();

  const { data: details, refetch } = useQuery({
    queryKey: ["MaintenanceListDetail", id],
    queryFn: async () => {
      return maintenanceDetails(id);
    },
    onError: (error) => {
      console.log(error);
    },
    select: (data) => data.results.details,
  });

  const changeStatus = async (v) => {
    const formData = {
      status: v,
    };
    const response = await statusUpdateMaintinanc(id, formData);
    if (!response.error) {
      showAlert(alert, response.message, { timeout: 3000 });
      refetch();
    } else {
      showAlert(alert, response.message, { timeout: 3000 });
    }
  };

  return (
    <>
      <Header />

      <div className="admin_main">
        <SideBar />
        <div className="admin_contentpart">
          <Link
            to={`/admin/maintenance-and-updates?page=${
              queryParams.get("page") || 1
            }`}
            className="comman_btn w-fit"
          >
            <i className="fa fa-arrow-left" />
          </Link>
          <div className="row comman_design mx-0 mt-4">
            <div className="col-12">
              <div className="row align-items-center justify-content-between py-md-4 py-3 px-md-3 px-2">
                <div className="col-md-auto mb-md-0 mb-3">
                  <div className="headleft">
                    <h2>Maintenance and Updates View</h2>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="row comman_table pt-md-3 pb-md-4 pb-3 px-md-3 px-2">
                <div className="col-12">
                  <div>
                    <div className="w-100">
                      {/* <div className="mt-3">
                        <span className="fs-6 fw-bold mb-0">Id : </span>
                        <span className="fs-6 fw-light mb-0">1</span>
                      </div> */}
                      <div className="mt-3">
                        <span className="fs-6 fw-bold mb-0">
                          Maintenance &amp; Updates Query :{" "}
                        </span>
                        <span className="fs-6 fw-light mb-0">
                          {details?.description}
                        </span>
                      </div>
                      <div className="mt-3">
                        <span className="fs-6 fw-bold mb-0">Start Date : </span>
                        <span className="fs-6 fw-light mb-0">
                          {moment(details?.startDate).format(
                            "DD MMM YYYY, hh:mm A"
                          )}
                        </span>
                      </div>
                      <div className="mt-3">
                        <span className="fs-6 fw-bold mb-0">End Date : </span>
                        <span className="fs-6 fw-light mb-0">
                          {" "}
                          {moment(details?.endDate).format(
                            "DD MMM YYYY, hh:mm A"
                          )}
                        </span>
                      </div>
                      <div className="mt-3">
                        <span className="fs-6 fw-bold mb-0">Status : </span>
                        <span
                          className={
                            details?.status
                              ? "status_accepted"
                              : "status_declained"
                          }
                        >
                          {details?.status ? "Active" : "Inactive"}
                        </span>
                      </div>
                      <div className="d-flex mt-2">
                        <strong>Status Update </strong>
                        <span>
                          <div className="ms-2 form-check form-switch custom-switch">
                            <input
                              className="form-check-input "
                              type="checkbox"
                              onChange={(e) => changeStatus(e.target.checked)}
                              checked={details?.status}
                            />
                            <label
                              className="form-check-label"
                              htmlFor="flexSwitchCheckChecked"
                            />
                          </div>
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default MaintenanceDetails;
